<template>
  <vue-editor class="editor" v-model="content"></vue-editor>
</template>

<script>
  import { VueEditor } from 'vue2-editor'

  export default {
    components: {
      VueEditor
    },
    data () {
      return {
        content: '<h1>Some initial content</h1>'
      }
    }
  }
</script>

<style>
  .editor {
    background-color: #ffffff;
  }
</style>




